import { DisplayObject, Point } from "pixi.js";
import { App } from "../App";
import { Component } from "../Component";

export function getHitPos(
  pos: Coordinate,
  obj: Component | DisplayObject
): Coordinate | null {
  if (obj instanceof Component) {
    obj = obj.ui;
  }
  const v: Float32Array = (<any>obj).vertexData;
  let { x, y } = pos;
  [x, y] = [x - v[0], y - v[1]];
  const [x1, y1] = [v[2] - v[0], v[3] - v[1]];
  const [x2, y2] = [v[6] - v[0], v[7] - v[1]];
  const x2_y1_x1_y2 = x2 * y1 - x1 * y2;
  let a = (x2 * y - x * y2) / x2_y1_x1_y2;
  let b = (x * y1 - x1 * y) / x2_y1_x1_y2;
  if (0 <= a && a <= 1 && 0 <= b && b <= 1) {
    return { x: a, y: b };
  }
  return null;
}

function isAttached(obj: DisplayObject) {
  while (obj != null) {
    if (obj === App.stage) {
      return true;
    }
    obj = obj.parent;
  }
  return false;
}

export function hitTest(pos: Coordinate, obj: Component) {
  if (!obj.visible) {
    return false;
  }
  const e: DisplayObject = (<any>obj).ui2;
  if (!isAttached(e)) {
    return false;
  }
  if (!e.hitArea) {
    return false;
  }
  const pt = new Point(pos.x, pos.y);
  e.worldTransform.applyInverse(pt, pt);
  return e.hitArea.contains(pt.x, pt.y);
}
